import React, { useState } from "react";
import "../Header/Header.scss";
import { Link } from "react-router-dom";
import { Header_Category } from "../../Shared/CategoryId";

const UserMenu = () => {
  return (
    <div className="UserMenu">
      <ul className="UserMenu_List">
        <li className="UserMenu_List_join">
          <Link to="/signup"></Link>
        </li>
        <li className="UserMenu_List_login">
          {" "}
          <Link to="/login"></Link>
        </li>
        <li className="UserMenu_List_ask"></li>
      </ul>
    </div>
  );
};

const Logo = () => {
  return (
    <div className="Logo">
      <Link to="/">
        <img
          className="Logo_img"
          src="https://res.kurly.com/images/marketkurly/logo/logo_x2.png"
        />
      </Link>
    </div>
  );
};

const Navbar = () => {
  const [Query, setQuery] = useState(null);
  const onChange = (e) => {
    const { value, name } = e.target;
    setQuery(value);

    console.log("Query", Query);
  };
  return (
    <div className="Navbar">
      <ul className="Navbar_main">
        <span>
          <li> </li>
        </span>
        <li>
          <Link
            to={{
              pathname: "/list/newBook",
              state: {
                value: "",

                key: Header_Category[0].key,
              },
            }}
          >
            
          </Link>
        </li>
        <li>
          <Link
            to={{
              pathname: "/list/bestSeller",
              state: {
                value: "",

                key: Header_Category[0].key,
              },
            }}
          >
            
          </Link>
        </li>
        <li>
          {" "}
          <Link
            to={{
              pathname: "/list/bestSeller",
              state: {
                value: "",

                key: Header_Category[1].key,
              },
            }}
          >
            
          </Link>
        </li>
        <li>
          {" "}
          <Link
            to={{
              pathname: "/list/bestSeller",
              state: {
                value: "/DVD",

                key: Header_Category[2].key,
              },
            }}
          >
            /DVD
          </Link>
        </li>
      </ul>
      <div className="Navbar_search">
        <form>
          <input
            type="text"
            name="Query"
            value={Query}
            onChange={onChange}
            placeholder=" "
          ></input>{" "}
          <Link
            to={{
              pathname: "/list/search",
              state: {
                value: Query,
              },
            }}
          >
            <input
              type="image"
              src="https://res.kurly.com/pc/service/common/1908/ico_search_x2.png"
            ></input>
          </Link>
        </form>
      </div>
      <div className="Navbar_loc">
        <button className="loc_btn"> </button>
      </div>
      <div className="Navbar_info">
        <button className="info_btn"></button>
      </div>
      <div className="Navbar_cart">
        <button className="cart_btn"></button>
      </div>
    </div>
  );
};

const Header = () => {
  return (
    <div className="Header">
      <UserMenu />
      <Logo />
      <Navbar />
    </div>
  );
};

export default Header;
