import React, { Component } from "react";
import "./Footer.scss";
import FooterInfoData from "./FooterInfoData";
import FooterIconData from "./FooterIconData";

const Footer = () => {
  return (
    <div id="footer">
      <div className="inner_footer">
        <div className="footer_cc">
          {" "}
          {/*right : customer counter*/}
          <h2 className="title_cc"> </h2>
          <div className="cc_view cc_call">
            <h3>
              <span className="tit">1644-1107</span>
            </h3>
            <dl className="list">
              <dt>365</dt>
              <dd>7 -  7</dd>
            </dl>
          </div>
          <div className="cc_view cc_kakao">
            <h3>
              <a className="tit"> </a>
            </h3>
            <dl className="list">
              <dt>365</dt>
              <dd>7 - 7</dd>
            </dl>
          </div>
          <div className="cc_view cc_qna">
            <h3>
              <a className="tit">1:1 </a>
            </h3>
            <dl className="list">
              <dt>24  </dt>
              <dd>   .</dd>
            </dl>
          </div>
          <div className="cc_view cc_bulkorder">
            <h3>
              <a className="tit"> </a>
            </h3>
            <p className="list">    .</p>{" "}
            {/*    p */}
          </div>
        </div>
        <div className="company">
          {" "}
          {/*left : about company*/}
          <ul className="list">
            <li>
              <a className="link"> </a>
            </li>
            <li>
              <a className="link"></a>
            </li>
            <li>
              <a className="link"></a>
            </li>
            <li>
              <a className="link"></a>
            </li>
            <li>
              <a className="link"></a>
            </li>
            <li>
              <a className="link"></a>
            </li>
          </ul>
          {/*  mapping  ??*/}
          <div className="company_info">
            {FooterInfoData.map((info) => {
              return (
                <>
                  {info.txt[0] !== "|" ? <br /> : ""}
                  <span className="info_txt">{info.txt}</span>
                  <span className="info_link">{info.link}</span>
                </>
              );
            })}
            ;
            <br />
            <span className="company__right">
               KURLY CORP. ALL RIGHTS RESERVED
            </span>
          </div>
          <ul className="sns_list">
            {FooterIconData.map((iconData) => {
              return (
                <li>
                  <a href={iconData.href} className="link_sns" target="_blank">
                    <img src={iconData.icon} alt={iconData.alt}></img>
                  </a>
                </li>
              );
            })}
          </ul>
        </div>
      </div>
      <div className="footer_link">
        <div className="authentication">
          <a className="mark">
            <img
              className="logo"
              src="https://res.kurly.com/pc/ico/2001/logo_isms.png"
              alt="isms "
            ></img>
            <ul className="txt">
              <li>[]      </li>
              <li>[] 2019.04.01 ~ 2022.03.31</li>
            </ul>
          </a>
          <a className="mark">
            <img
              className="logo"
              src="https://res.kurly.com/pc/ico/2001/logo_eprivacyplus.png"
              alt="eprivacy plus "
            ></img>
            <ul className="txt">
              <li>   </li>
              <li>  (ePRIVACY PLUS)</li>
            </ul>
          </a>
          <a className="mark">
            <img
              className="logo"
              src="https://res.kurly.com/pc/service/main/2009/logo_payments.png"
              alt="payments "
            ></img>
            <ul className="txt">
              <li>
                        
                
              </li>
              <li>
                  ()    .
              </li>
            </ul>
          </a>
        </div>
      </div>
    </div>
  );
};

export default Footer;
